import React, {Component,PropTypes} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {fetchYears,fetchMakes,fetchModels,fetchTrims,fetchOptions,setModel,setOption} from '../../actions/vehicle-selector';
import Modal from 'react-modal';
import {OPTIONS_REMOVED} from '../reducers';
import {sortBy} from '../../services/sort';
var unselectOption = function(opt){
    return {
            type:OPTIONS_REMOVED,
            payload:opt
    }
}
class OptionSelectorComponent extends Component
{
    componentDidMount()
    {
         this.props.source.registerOption(this);
         this.optionChangedHandler = this.optionChangedHandler.bind(this);
    }
    optionChangedHandler(){

    }
    changedHandler(id,e){
        if(e.target.checked)
            this.optionChangedHandler(id);
        else
            this.props.unselectOption(id);
    }
    render(){
        function toLower(s){
            return s.toLowerCase();
        }
        var filteredOptions = this.props.filter ? 
            this.props.options.filter((o)=> o.description.toLowerCase().indexOf(this.props.filter.toLowerCase()) > -1).sort(sortBy('description',false,toLower))
            : this.props.options;
        var checkboxes = filteredOptions ? filteredOptions.map((o)=>{
            var checked = this.props.selectedOptions && this.props.selectedOptions.indexOf(o.id) > -1;
            return <div  key={o.id}><input checked={checked} onChange={this.changedHandler.bind(this,o.id)} id={o.id} name={o.id} type="checkbox"/><label htmlFor={o.id}>{o.description}</label></div>
        }) : [];
        return (
            <div style={this.props.style}>
                {checkboxes}
             </div>
        )
    }
}
function mapStateToProps(state){
    return {
        models:state.vehicleSelectorModels,
        year: state.vehicleSelectorYear,
        make: state.vehicleSelectorMake,
        options:state.vehicleSelectorOptions,
        selectedOptions: state.selectedOptions 
    }
}

function mapDispatchToProps(dispatch){
    return bindActionCreators({setOption,unselectOption},dispatch);
}
var OptionSelector = connect(mapStateToProps,mapDispatchToProps)(OptionSelectorComponent);

export {OptionSelector};



